.has-background-overlay:not(.has-background-image) {
  background-color: #000; }

.has-background-video {
  position: relative; }
  .has-background-video .pin-t {
    top: 0; }
  .has-background-video .pin-b {
    bottom: 0; }
  .has-background-video .pin-l {
    left: 0; }
  .has-background-video .pin-t {
    right: 0; }
  .has-background-video .object-cover {
    object-fit: cover; }
  .has-background-video .object-position {
    object-position: center; }

.has-background-image.has-parallax {
  background-attachment: fixed; }
  @supports (-webkit-overflow-scrolling: touch) {
    .has-background-image.has-parallax {
      background-attachment: scroll; } }
  @media (prefers-reduced-motion: reduce) {
    .has-background-image.has-parallax {
      background-attachment: scroll; } }

.has-background-image.has-background-overlay {
  position: relative; }
  .has-background-image.has-background-overlay::before {
    content: "";
    background-color: inherit;
    bottom: 0;
    left: 0;
    opacity: 0.5;
    position: absolute;
    right: 0;
    top: 0; }

.has-background-image.has-background-overlay-10::before {
  opacity: 0.1; }

.has-background-image.has-background-overlay-20::before {
  opacity: 0.2; }

.has-background-image.has-background-overlay-30::before {
  opacity: 0.3; }

